@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@200&family=Palette+Mosaic&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
    font-family: 'Josefin Sans', sans-serif;
    
}

.App-logo {
  height: 40vmin;
  pointer-events: none;


}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #333;
  min-height: 100vh;
  width:100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.flex{
display: flex;
flex-direction:row;
}


/*  sidebar */

.togg{
position: fixed;
width: 20vh;
left: 5rem;
z-index:2;
}

.togg:hover{
transform:rotate(30deg);
cursor:pointer;
transition:1s ease-in;
}


.sidebar{
padding:2vh;
text-align: center;
}

.sidebar h1{
background:green;
width: 50px;
height: 50px;
margin:10px auto;
color: #fff;
border-radius: 50%;
font-family: 'Palette Mosaic', cursive;
}

.navcolor{
color:green;
}

.cartoon{
position: fixed;
left: 10rem;
top: -4rem;
}

/* collection */
.collection{
width: 90%;	
margin:auto;
}

.collection .item{
float: left;
}

.item img{
height: 500px;
width: 360px;
opacity: 0.4;
}

.item img:hover{
opacity: 1;
cursor: pointer;
}





